                            <opinion-list :action-id="action.id" v-if="loadOpinion"></opinion-list>


                            <div class="opinion-add">
                                <a class="link link-icon" href="javascript:void(0);" @click="openOpinionAdd">
                                    <span class="i i-plus3"></span>
                                    <span class="t"> </span>
                                </a>
                            </div>
                            <!-- end opinions-list -->
