                                      <span class="t"> </span>
                                  </button>
                              </div>

                              <div class="coupon-details-mini__btn" v-else-if="+action.status === 19 && isCityDiff && action.isBuy" > <!-- v-else-if="+action.status === 19 && isCityDiff && action.isBuy" -->
                                  <router-link tag="button" to="/personal"
                                               class="btn btn--white-yellow">
                                      <span class="t"> </span>
                                  </router-link>
                              </div>

                              <div class="coupon-details-mini__status" v-else-if="+action.status === 20 || +action.status === 21"> <!-- v-else-if="+action.status === 20 || +action.status === 21" -->
                                  <div v-if="action.banReason && !action.banProvider">
                                       <br>.
                                      : {{ action.banReason }}
                                  </div>

                                  <div v-else-if="action.banReason && action.banProvider">
                                        <br>
                                         .<br>:
                                      {{ action.banProvider }}
                                  </div>

                                  <div v-else>
                                       <br>
                                         <br>  .
                                  </div>
                              </div>


                              <div class="coupon-details-mini__btn" v-else> <!-- v-else -->
