                                            <br>
                                             :(
                                      </span>

                                      <span class="n-city">
                                           <br> . {{ cityDiffMain.name }}<br>
                                          <tooltip v-if="citiesDiff.length > 1"
                                                   :direction="'bottom-center-direction'"
                                                   :add-class-wrap="'dib'"
                                                   :add-style="{width: 284 + 'px'}">
                                              <br><span slot="tooltipBtn" class="pseudo-link link-color"> {{ citiesDiff.length - 1 }} </span>

                                              <div slot="tooltipContent">
                                                  <p>
                                                      <strong>
                                                          {{ citiesDiffImplode }}
                                                      </strong>
                                                  </p>
                                              </div>
                                          </tooltip>
                                      </span>
                                  </span>
                              </div>


                              <div class="coupon-details-mini__stat">
