                <div class="mini-zems"></div>
            </div>
            <div v-else class="preloader-page">
                <div class="preloader"></div>
            </div>
        </div>

        <div v-else class="preloader-page">
            <div class="preloader"></div>
        </div>
        <popup v-if="action" :is-btn-close="opinionError === false"
               :show="popups.opinionAdd.show"
               :is-set-styled="opinionError === false"
               :name="popups.opinionAdd.name">
            <opinion-error-send :action="action" v-if="opinionError === 'isSend'"></opinion-error-send>
            <opinion-error :action="action" v-else-if="opinionError === 'notOrder'"></opinion-error>
            <opinion-add :action="action" v-else></opinion-add>
        </popup>


        <popup v-if="action" :is-set-styled="true" :show="popups.couponClaim.show" :name="popups.couponClaim.name">
            <coupon-claim :action="action"></coupon-claim>
        </popup>


        <popup v-if="action" :is-set-styled="false" :show="popups.couponBuy.show" :name="popups.couponBuy.name" :width="528" :add-class="'popup--order'">
            <auth :show-header="false" :is-order="true" v-if="!token && !isBanned">
            </auth>
            <many-auth v-if="isBanned"></many-auth>
            <coupon-buy :action="action" v-if="token && !isBanned"></coupon-buy>
        </popup>

        <popup v-if="action" :show="popups.couponBuyOk.show" :name="popups.couponBuyOk.name">
            <coupon-buy-ok></coupon-buy-ok>
        </popup>

        <popup v-if="action" :show="popups.couponBuyError.show" :name="popups.couponBuyError.name">
            <coupon-buy-error></coupon-buy-error>
        </popup>

        <popup v-if="action" :show="popups.couponBuyErrorAuth.show" :name="popups.couponBuyErrorAuth.name">
            <coupon-buy-auth-error></coupon-buy-auth-error>
        </popup>
        <popup v-if="action !== undefined && action.id && action.comeUsJson" :show="popups.address.show" :name="popups.address.name" :add-class="'popup--map'" :is-set-blank="true">
            <Map :markers="getMarkers()"
                 :height="558"
                 :coords="mapCenter"
                 :zoom="11"
            ></Map>
        </popup>
    </div>

</template>



<script>
    import {mapState} from 'vuex';
    import axios from 'axios';
    import Tabs from '@/components/tabs/Tabs.vue';
    import Tab from '@/components/tabs/Tab.vue';
    import Popup from '@/components/modal/Popup.vue';
    import OpinionAdd from '@/components/modal/OpinionAdd.vue';
    import OpinionList from '@/components/OpinionList.vue';
    import OpinionError from '@/components/modal/OpinionError.vue';
    import OpinionErrorSend from '@/components/modal/OpinionErrorSend.vue';
    import CouponClaim from '@/components/modal/CouponClaim.vue';
    import MailZems from '@/components/modal/MailZems.vue';
    import {declOfNumber} from '@/helpers/formatted';
    import CouponBuy from '@/components/modal/CouponBuy.vue';
    import CouponBuyOk from '@/components/modal/CouponBuyOk.vue'; //   "   !"
    import CouponBuyError from '@/components/modal/CouponBuyError.vue'; //   "-   ..."
    import CouponBuyAuthError from '@/components/modal/CouponBuyAuthError.vue'; //   "-   ..."
    import Auth from '@/components/user/auth/Auth.vue';
    import ManyAuth from '@/components/modal/ManyAuth.vue';
    import Tooltip from '@/components/Tooltip.vue';
    import CatalogList from '@/components/catalog/CatalogList.vue';
    import StatisticsDetail from '@/components/catalog/StatisticsDetail.vue';
    import Map from '@/components/Map.vue';
    import PartnerAboutTextTab from '@/components/partner/PartnerAboutTextTab.vue';
    import CatalogListItem from '@/components/catalog/CatalogListItem.vue';
    import Gallery from '@/components/Gallery.vue';
    import {flyToFavorite} from '@/helpers/animate';
    import {actionDiscountHtml} from '@/helpers/tempateHtml';
    import AsideSticky from '@/components/AsideSticky.vue';
    import LineChart from '@/components/LineChart.vue';
    import PieChart from '@/components/PieChart.vue';
    import LabelHint from '@/components/forms/LabelHint.vue'

    export default {
        name: "CatalogDetail",
        components: {
            Tabs,
            Tab,
            Map,
            Popup,
            OpinionAdd,
            OpinionList,
            OpinionError,
            OpinionErrorSend,
            CouponClaim,
            MailZems,
            CouponBuy,
            CouponBuyOk,
            CouponBuyError,
            CouponBuyAuthError,
            Auth,
            ManyAuth,
            CatalogList,
            Tooltip,
            StatisticsDetail,
            PartnerAboutTextTab,
            CatalogListItem,
            Gallery,
            AsideSticky,
            LineChart,
            PieChart,
            LabelHint
        },
        data() {
            return {
                typeReport: 'notUse', // trouble
                currentTab: '',
                loadOpinion: false,
                mapCenter: [55.427862,37.550162],
                isTabSupplier: false,
                init: false,
                aside: null,
                newBlock: null,
                fixedMargin: null,
                z: null,
                marginTop: 24,
                marginBottom: 24,
                showFirstPhoto: false,
            }
        },
        mounted() {
            this.$store.dispatch('actions/initDetailPage', {route: this.$route}).then(()=>{
                this.init = true;
            });
        },
        watch:{
          '$route'(to,from){
            this.newBlock.className = 'stop';
            this.newBlock.style.top = '0px';
          }
        },
        beforeDestroy(){
            this.$store.commit('opinions/clearData', {});
            this.$store.commit('actions/setCouponDetail', {});
        },
        computed: {
            token(){
                return this.localStorage.token !== 'false' && this.localStorage.token !== null && this.localStorage.token !== undefined ? this.localStorage.token : false
            },
            countHowToUse(){
                let i = 0;
                if(this.action.callUsJson){
                    i++;
                }
                if(this.action.comeUsJson){
                    i++;
                }
                if(this.action.goLinkJson){
                    i++;
                }
                return i;
            },
            ...mapState({
                popups: state => state.popups.items,
                action: state => state.actions.detail,
                similars: state => state.actions.detail.similars,
                user: state => state.users.user,
                isBanned: state => state.auth.isBanned,
                isLoadDetail: state => state.actions.isLoadDetail,
                currentCity: state => state.cities.current,
                cities: state => state.cities.items,
                visited: state => state.actions.visited,
                errorLoad: state => state.actions.errorLoad,
                previousSection: state => state.actions.previousSection,
            }),
            backSection() {
                let sectionID = this.action.section.id;

                if (this.previousSection && this.action.sectionNames[this.previousSection]) {
                    sectionID = this.previousSection;
                }

                return {id: sectionID, name: this.action.sectionNames[sectionID]};
            },
            cityDiffMain() {
                let city =  this.citiesDiff.find(c => c.isCapital === true);
                if(!city && this.citiesDiff.length > 0) {
                    city = this.citiesDiff[0];
                }
                if(!city) {
                    city = {};
                }
                return city;
            },
            citiesDiffImplode() {
                let cities = this.citiesDiff.filter(c => c.xmlId !== this.cityDiffMain.xmlId);
                let citiesImplode  = [];
                for(let city of cities){
                    citiesImplode.push(city.name);
                }
                return citiesImplode.join(', ');
            },
            citiesDiff() {
                let result = [];
                for(let city of this.action.city) {
                    let findCity = [...this.cities].find(c => c.xmlId === city);
                    if(findCity){
                        result.push(findCity);
                    }
                }
                return result;
            },
            isCityDiff() {
                return this.action.city.find(c => c === this.currentCity.xmlId || c === 'all');
            },
            opinionError() {
                if(this.user.id && this.action){
                    for (let opinion of this.user.opinions){
                        if(+opinion.action === +this.action.id && +opinion.action > 0) {
                            return 'isSend'
                        }
                    }
                    let isOrder = false;
                    for (let order of this.user.orders){
                        if(+order.action === +this.action.id && +order.action > 0) {
                            isOrder = true;
                        }
                    }
                    if(!isOrder){
                        return 'notOrder'
                    }
                }
                return false
            },
            originDiscountHtml(){
                return this.action.originDiscount ? actionDiscountHtml(this.action.originDiscount, this.action.discount) : ''
            },
            onenessDiscountHtml(){
                return this.action.onenessDiscount ? actionDiscountHtml(this.action.onenessDiscount, this.action.discount) : ''
            },
        },
        methods: {
            downloadMainImage(){
                window.open('//api.zemskidki.ru'+this.action.image_original,'_blank');
            },
            showViewer(){
                this.showFirstPhoto = new Date();
            },
            openSupplierInfo(){
              this.$store.dispatch('popups/open', {name: this.popups.supplierInfo.name});
            },
            balloonTemplate(address) {
                return `
                  <div class="yamap_baloon_link yamap_baloon_link_address hover">
                    <img src="//api.zemskidki.ru/` + this.action.image + `" alt="` + this.action.name + `">
                    ` + this.action.provider.name + `<br>
                    <b class="yamap_baloon_link-name">` + this.action.name + `</b><br/>
                    ` + address + `
                  </div>
                `
            },
            openMap: function(key) {
                let markers = {},
                    coords;

                if (this.action && this.action.comeUsJson.coord.value) {
                    coords = this.action.comeUsJson.coord.value.split(',');

                    markers[0] = [coords[1], coords[0]];

                    for (let index in this.action.comeUsJson.otherAddresses) {
                        let coords = this.action.comeUsJson.otherAddresses[index].coord.value.split(',');
                        markers[parseInt(index) + 1] = [coords[1], coords[0]];
                    }

                    this.mapCenter = markers[key];
                    this.$store.dispatch('popups/open', {name: this.popups.address.name});
                }
            },
            getMarkers: function () {
                let markers = [];
                if (this.action.comeUsJson.coord.value) {
                    let coords = this.action.comeUsJson.coord.value.split(',');

                    markers.push({
                        "id": 10,
                        "type": "placemark",
                        "coords": [coords[1], coords[0]],
                        "hint": "",
                        "balloon": {
                            "header": "",
                            "body": this.balloonTemplate(this.action.comeUsJson.address.value),
                            "footer": ""
                        },
                        "cluster": "1"
                    });

                }

                for (let index in this.action.comeUsJson.otherAddresses) {
                    if (this.action.comeUsJson.otherAddresses[index].coord.value) {
                        let coords = this.action.comeUsJson.otherAddresses[index].coord.value.split(',');
                        let obj = {
                            "id": parseInt(index) + 1,
                            "type": "placemark",
                            "coords": [coords[1], coords[0]],
                            "hint": "",
                            "balloon": {
                                "header": "",
                                "body": this.balloonTemplate(this.action.comeUsJson.otherAddresses[index].address.value),
                                "footer": ""
                            },
                            "cluster": "1"
                        };

                        markers.push(obj);
                    }
                }

                return markers;
            },
            openOpinionAdd: function () {
                if(!!this.user.id){
                    this.$store.dispatch('popups/open', {name: this.popups.opinionAdd.name});
                } else {
                    this.$store.commit('auth/setIsRedirect', false);
                    this.$store.dispatch('popups/open', {name: this.popups.publicAuth.name});
                }
            },
            openMailZems: function (typeReport) {
                this.typeReport = typeReport;
                this.$store.dispatch('popups/open', {name: this.popups.mailZems.name});
            },
            openCouponClaim: function () {
                this.$store.dispatch('popups/open', {name: this.popups.couponClaim.name});
                this.$store.commit('auth/setIsRedirect', false);
            },
            openCouponBuy: function () {
                this.checkToken();
                this.$store.commit('auth/setIsRedirect', false);
                this.$store.dispatch('popups/open', {name: this.popups.couponBuy.name});
            },
            setFavorite: function () {
                let action = {...this.action};
                action.isDetail = true;
                this.$store.dispatch('actions/setFavorite', action);
            },
            activeSection: function (id) {
                if(id){
                    this.$store.commit('actions/setCouponList', {items: []});
                    this.$store.commit('actions/setPage', 0);
                    this.$store.commit('actions/previousSection', id);
                    this.$store.commit('actions/setViewType', 'list');
                    this.$router.push('/sid'+id)
                    //this.$store.commit('actions/activeCategory', {currentSection: id});
                    /*this.$store.dispatch('actions/activeCategory', {
                        currentSection:id,
                        isLoad: true,
                    });*/

                }
            },
            declOfNumber(number, titles){
                return declOfNumber(number, titles);
            },
            changeTab(data) {
                this.currentTab = data.tab.hash;
                if(this.currentTab === '#tab-opinions' && this.action.id){
                    this.loadOpinion = true;
                }

                this.isTabSupplier = (this.currentTab === '#tab-supplier') ? false : true;
            },
            setTabSupplier() {
                document.querySelector("[aria-controls='#tab-supplier']").click();
            },
            checkToken(){
                if(!checkToken){
                    this.localStorage.token = 'false';
                    this.$store.dispatch('auth/logout');
                }
            },
            reload(){
                window.location.href = window.location.href;
            },
        }

    }
    function checkToken(){
        return localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined
    }
</script>

<style>
    .yamap_baloon_link.yamap_baloon_link_address {
        position: relative;
        background-size: 100px;
        padding-left: 115px;
        min-height: 100px;
    }

    .yamap_baloon_link.yamap_baloon_link_address img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100px;
        height: 100px;
        border-radius: 10px;
    }

    .yamap_baloon_link.yamap_baloon_link_address .yamap_baloon_link-name {
        color: #2F80ED;
        font-size: 14px;
    }
    .download_icon{
        position: absolute;
        z-index: 1000;
        right: 15px;
        top: 5px;
        cursor: pointer;
    }
</style>
